eval("/* eslint-env amd */\n\ndefine(\'job-contract/filters/get-obj-by-id.filter\',[],function () {\n  \'use strict\';\n\n  getObjById.__name = \'getObjById\';\n  getObjById.$inject = [\'$log\'];\n\n  function getObjById ($log) {\n    $log.debug(\'Filter: getObjById\');\n\n    return function (input, id, key) {\n      if (!input) {\n        return null;\n      }\n\n      var i = 0;\n      var len = input.length;\n\n      for (; i < len; i++) {\n        if (+input[i].id === +id) {\n          return !key ? input[i] : input[i][key];\n        }\n      }\n      return null;\n    };\n  }\n\n  return getObjById;\n});\n\n//# sourceURL=/job-contract/filters/get-obj-by-id.filter.js");

