eval("/* eslint-env amd */\n\ndefine(\'job-contract/filters/capitalize.filter\',[],function () {\n  \'use strict\';\n\n  capitalize.__name = \'capitalize\';\n  capitalize.$inject = [\'$log\'];\n\n  function capitalize ($log) {\n    $log.debug(\'Filter: capitalize\');\n\n    return function (input) {\n      return (input) ? input.replace(/([^\\W_]+[^\\s-]*) */g, function (txt) { return txt.charAt(0).toUpperCase() + txt.substr(1).toLowerCase(); }) : \'\';\n    };\n  }\n\n  return capitalize;\n});\n\n//# sourceURL=/job-contract/filters/capitalize.filter.js");

